// ApacheConfig.jsx  -       
import React, { useState, useRef } from "react";
import { HelpCircle } from "lucide-react";
import { useOutletContext } from "react-router-dom";

export default function ApacheConfig() {
  const [showHint, setShowHint] = useState(null);
  const [toggles, setToggles] = useState({});
  const hintRefs = useRef({});
  const { setSelectedDocKey, setDocContent } = useOutletContext();

  const toggleSwitch = (key) => {
    setToggles((prev) => ({ ...prev, [key]: !prev[key] }));
  };

const loadMarkdown = async (label) => {
  try {
    const response = await fetch(`/descriptions/${label}.md`);
    const text = await response.text();
    setDocContent(text);
    setSelectedDocKey(label);
  } catch (err) {
    console.error(`   : ${label}`, err);
    setDocContent("   .");
    setSelectedDocKey(label);
  }
};

  const renderSetting = (label, input, hint, description) => (
    <div style={{ marginBottom: "1.5rem", position: "relative", zIndex: 0 }}>
      <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between" }}>
        <div style={{ width: "40%", position: "relative" }}>
          <label style={{ fontWeight: "bold", display: "flex", alignItems: "center" }}>
            {label}
            <span style={{ display: "flex", alignItems: "center", marginLeft: "8px" }}>
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  setShowHint(label);
                }}
                style={{
                  background: "none",
                  border: "none",
                  cursor: "pointer",
                  color: "#ccc",
                  padding: 0,
                  display: "flex",
                  alignItems: "center"
                }}
                title="  "
                ref={(el) => (hintRefs.current[label] = el)}
              >
                <HelpCircle size={16} />
              </button>
            </span>
          </label>
          <div style={{ fontSize: "0.85rem", color: "#aaa", marginTop: "0.25rem" }}>{description}</div>
        </div>
        <div style={{ width: "55%", textAlign: "right" }}>{input}</div>
      </div>
{showHint === label && (
  <div
    style={{
      position: "absolute",
      left: hintRefs.current[label]?.offsetLeft ?? 0,
      top: (hintRefs.current[label]?.offsetTop ?? 0) + 24,
      backgroundColor: "#fff",
      color: "black",
      padding: "10px 12px",
      borderRadius: "8px",
      boxShadow: "0 4px 8px rgba(0,0,0,0.2)",
      minWidth: "240px",
      zIndex: 9999
    }}
  >
    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginBottom: "4px" }}>
      <strong>{label}</strong>
      <span style={{ display: "flex", gap: "6px" }}>
        {/*      */}
<button
  onClick={() => loadMarkdown(label)}
  title=" "
  style={{
    background: "none",
    border: "none",
    padding: 0,
    margin: 0,
    color: "#555", //    
    fontSize: "1.1rem", //   
    cursor: "pointer"
  }}
>
  
</button>
<button
  onClick={() => setShowHint(null)}
  title=""
  style={{
    background: "none",
    border: "none",
    padding: 0,
    margin: 0,
    color: "#555",
    fontSize: "1.1rem",
    cursor: "pointer"
  }}
>
  
</button>
      </span>
    </div>
    <div style={{ fontSize: "0.9rem" }}>{hint}</div>
  </div>
)}

    </div>
  );

  const renderToggle = (key) => (
    <div
      onClick={() => toggleSwitch(key)}
      style={{
        display: "inline-block",
        width: "46px",
        height: "24px",
        backgroundColor: toggles[key] ? "#4ade80" : "#888",
        borderRadius: "24px",
        position: "relative",
        cursor: "pointer",
        transition: "background-color 0.3s"
      }}
    >
      <div
        style={{
          position: "absolute",
          top: "3px",
          left: toggles[key] ? "24px" : "3px",
          width: "18px",
          height: "18px",
          backgroundColor: "white",
          borderRadius: "50%",
          transition: "left 0.3s"
        }}
      />
    </div>
  );


return (
    <div style={{ padding: "1rem", color: "white" }}>
      <h2 style={{ fontSize: "1.5rem", fontWeight: "bold", marginBottom: "1.5rem" }}>
        Apache httpd.conf  ( UI)
      </h2>

      <div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
        <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}> Server Settings</h3>
        {renderSetting("ServerAdmin", <input type="email" placeholder="webmaster@example.com" style={inputStyle} />, "     .", "   ")}
        {renderSetting("ServerName_global", <input type="text" placeholder="localhost" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("DocumentRoot", <input type="text" placeholder="/var/www/html" style={inputStyle} />, "    .", "   ")}
        {renderSetting("ServerTokens", <select style={inputStyle} defaultValue="Prod">
          <option value="Full">Full</option>
          <option value="OS">OS</option>
          <option value="Minimal">Minimal</option>
          <option value="Minor">Minor</option>
          <option value="Major">Major</option>
          <option value="Prod">Prod</option>
        </select>, "      .", "   ")}
        {renderSetting("HostnameLookups", renderToggle("HostnameLookups"), " IP  .  .", "   ")}
        {renderSetting("Timeout", <input type="number" placeholder="60" style={inputStyle} />, "    ().", "  ")}
      </div>

      <div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
        <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}> Logging</h3>
        {renderSetting("LogLevel", <select style={inputStyle} defaultValue="warn">
          <option value="debug">debug</option>
          <option value="info">info</option>
          <option value="notice">notice</option>
          <option value="warn">warn</option>
          <option value="error">error</option>
          <option value="crit">crit</option>
          <option value="alert">alert</option>
          <option value="emerg">emerg</option>
        </select>, "    .", "  ")}
        {renderSetting("ErrorLog", <input type="text" placeholder="logs/error_log" style={inputStyle} />, "    .", "  ")}
        {renderSetting("CustomLog", <input type="text" placeholder="logs/access_log common" style={inputStyle} />, "     .", "  ")}
      </div>

      <div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
        <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}> Access Control</h3>
        {renderSetting("Require", <input type="text" placeholder="all granted" style={inputStyle} />, "   . : all granted", "  ")}
        {renderSetting("AllowOverride", <select style={inputStyle} defaultValue="All">
          <option value="None">None</option>
          <option value="All">All</option>
        </select>, ".htaccess    .", "   ")}
      </div>

      <div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
        <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}> Directory Options</h3>
        {renderSetting("Indexes", renderToggle("Indexes"), "   .", "  ")}
        {renderSetting("FollowSymLinks", renderToggle("FollowSymLinks"), "   .", "   ")}
        {renderSetting("SymLinksIfOwnerMatch", renderToggle("SymLinksIfOwnerMatch"), "     .", "    ")}
        {renderSetting("ExecCGI", renderToggle("ExecCGI"), "CGI   .", "CGI  ")}
      </div>

      <div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
        <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}> Modules ()</h3>
        {renderSetting("LoadModule env_module", renderToggle("env_module"), "    .", "mod_env  ")}
        {renderSetting("LoadModule mime_module", renderToggle("mime_module"), "MIME    .", "mod_mime  ")}
        {renderSetting("LoadModule alias_module", renderToggle("alias_module"), "URL   .", "mod_alias  ")}
        {renderSetting("LoadModule dir_module", renderToggle("dir_module"), "     .", "mod_dir  ")}
        {renderSetting("LoadModule status_module", renderToggle("status_module"), "     .", "mod_status  ")}
        {renderSetting("LoadModule autoindex_module", renderToggle("autoindex_module"), "   .", "mod_autoindex  ")}
        {renderSetting("LoadModule include_module", renderToggle("include_module"), "   .", "mod_include  ")}
        {renderSetting("LoadModule negotiation_module", renderToggle("negotiation_module"), "   .", "mod_negotiation  ")}
        {renderSetting("LoadModule auth_basic_module", renderToggle("auth_basic_module"), "   .", "mod_auth_basic  ")}
        {renderSetting("LoadModule authn_file_module", renderToggle("authn_file_module"), "    .", "mod_authn_file  ")}
        {renderSetting("LoadModule authz_host_module", renderToggle("authz_host_module"), "     .", "mod_authz_host  ")}
        {renderSetting("LoadModule authz_user_module", renderToggle("authz_user_module"), "    .", "mod_authz_user  ")}
      </div>  

      <div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
  <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}> UserDir</h3>
  {renderSetting("UserDir", <input type="text" placeholder="public_html" style={inputStyle} />, "       .", "UserDir  ")}
</div>

<div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
  <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}> AutoIndex</h3>
  {renderSetting("IndexOptions", <input type="text" placeholder="FancyIndexing VersionSort" style={inputStyle} />, "   .", " ")}
  {renderSetting("AddDescription", <input type="text" placeholder="README.html" style={inputStyle} />, "     .", "  ")}
</div>

<div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
  <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}> Types </h3>
  {renderSetting("TypesConfig", <input type="text" placeholder="/etc/mime.types" style={inputStyle} />, "MIME     .", "MIME   ")}
  {renderSetting("DefaultType", <input type="text" placeholder="text/plain" style={inputStyle} />, "    MIME .", " MIME ")}
</div>

<div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
  <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}> Include</h3>
  {renderSetting("IncludeOptional", <input type="text" placeholder="conf.d/*.conf" style={inputStyle} />, "    .", " conf  ")}
</div>

<div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
  <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}> VirtualHost</h3>
  {renderSetting("<VirtualHost *:80>", <input type="text" placeholder="* or 192.168.0.1" style={inputStyle} />, "   IP: .", " ")}
  {renderSetting("ServerName_vhost", <input type="text" placeholder="www.example.com" style={inputStyle} />, "     .", " ")}
  {renderSetting("DocumentRoot", <input type="text" placeholder="/var/www/example" style={inputStyle} />, "     .", "  ")}
  {renderSetting("ErrorLog", <input type="text" placeholder="logs/example_error.log" style={inputStyle} />, "    .", "  ")}
  {renderSetting("CustomLog", <input type="text" placeholder="logs/example_access.log common" style={inputStyle} />, "    .", "  ")}
</div>

    </div>
  );
}

  const inputStyle = {
    padding: "6px 10px",
    width: "240px",
    backgroundColor: "#1e1f22",
    color: "white",
    border: "1px solid #555",
    borderRadius: "4px",
    textAlign: "right"
  };
