// ApacheConfig.jsx  -       
import React, { useState, useRef } from "react";
import { HelpCircle } from "lucide-react";
import { useOutletContext } from "react-router-dom";

export default function ApacheConfig() {
  const [showHint, setShowHint] = useState(null);
  const [toggles, setToggles] = useState({});
  const hintRefs = useRef({});
  const { setSelectedDocKey, setDocContent } = useOutletContext();

  const toggleSwitch = (key) => {
    setToggles((prev) => ({ ...prev, [key]: !prev[key] }));
  };

const loadMarkdown = async (label) => {
  try {
    const response = await fetch(`/descriptions/${label}.md`);
    const text = await response.text();
    setDocContent(text);
    setSelectedDocKey(label);
  } catch (err) {
    console.error(`   : ${label}`, err);
    setDocContent("   .");
    setSelectedDocKey(label);
  }
};

  const renderSetting = (label, input, hint, description) => (
    <div style={{ marginBottom: "1.5rem", position: "relative", zIndex: 0 }}>
      <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between" }}>
        <div style={{ width: "40%", position: "relative" }}>
          <label style={{ fontWeight: "bold", display: "flex", alignItems: "center" }}>
            {label}
            <span style={{ display: "flex", alignItems: "center", marginLeft: "8px" }}>
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  setShowHint(label);
                }}
                style={{
                  background: "none",
                  border: "none",
                  cursor: "pointer",
                  color: "#ccc",
                  padding: 0,
                  display: "flex",
                  alignItems: "center"
                }}
                title="  "
                ref={(el) => (hintRefs.current[label] = el)}
              >
                <HelpCircle size={16} />
              </button>
            </span>
          </label>
          <div style={{ fontSize: "0.85rem", color: "#aaa", marginTop: "0.25rem" }}>{description}</div>
        </div>
        <div style={{ width: "55%", textAlign: "right" }}>{input}</div>
      </div>
{showHint === label && (
  <div
    style={{
      position: "absolute",
      left: hintRefs.current[label]?.offsetLeft ?? 0,
      top: (hintRefs.current[label]?.offsetTop ?? 0) + 24,
      backgroundColor: "#fff",
      color: "black",
      padding: "10px 12px",
      borderRadius: "8px",
      boxShadow: "0 4px 8px rgba(0,0,0,0.2)",
      minWidth: "240px",
      zIndex: 9999
    }}
  >
    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginBottom: "4px" }}>
      <strong>{label}</strong>
      <span style={{ display: "flex", gap: "6px" }}>
        {/*      */}
<button
  onClick={() => loadMarkdown(label)}
  title=" "
  style={{
    background: "none",
    border: "none",
    padding: 0,
    margin: 0,
    color: "#555", //    
    fontSize: "1.1rem", //   
    cursor: "pointer"
  }}
>
  
</button>
<button
  onClick={() => setShowHint(null)}
  title=""
  style={{
    background: "none",
    border: "none",
    padding: 0,
    margin: 0,
    color: "#555",
    fontSize: "1.1rem",
    cursor: "pointer"
  }}
>
  
</button>
      </span>
    </div>
    <div style={{ fontSize: "0.9rem" }}>{hint}</div>
  </div>
)}

    </div>
  );

  const renderToggle = (key) => (
    <div
      onClick={() => toggleSwitch(key)}
      style={{
        display: "inline-block",
        width: "46px",
        height: "24px",
        backgroundColor: toggles[key] ? "#4ade80" : "#888",
        borderRadius: "24px",
        position: "relative",
        cursor: "pointer",
        transition: "background-color 0.3s"
      }}
    >
      <div
        style={{
          position: "absolute",
          top: "3px",
          left: toggles[key] ? "24px" : "3px",
          width: "18px",
          height: "18px",
          backgroundColor: "white",
          borderRadius: "50%",
          transition: "left 0.3s"
        }}
      />
    </div>
  );



return (
    <div style={{ padding: "1rem", color: "white" }}>
      <h2 style={{ fontSize: "1.5rem", fontWeight: "bold", marginBottom: "1.5rem" }}>
        BIND DNS   ( UI)
      </h2>


      <div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>



        <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}> /etc/named.conf</h3>
        {renderSetting("listen-on port 53", <input type="email" placeholder="webmaster@example.com" style={inputStyle} />, "     .", "   ")} 
        {renderSetting("listen-on-v6 port 53", <input type="text" placeholder="localhost" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("forward", <select style={inputStyle} defaultValue="Prod">
          <option value="only">only</option>
          <option value="first">first</option>
        </select>, "      .", "   ")}
        {renderSetting("forwarders", <input type="text" placeholder="localhost" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("allow-query", <input type="text" placeholder="192.168.0.1" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("allow-transfer", <input type="text" placeholder="localhost" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("acl",<input type="text" placeholder="member { 210.96.52.100; 203.247.40/24; 211.58.96.100; }" style={inputStyle}/>,
        "  IP   .", " ")}
      </div>


      <div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
        <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}> zone </h3>
        
        {renderSetting("zone''IN", <input type="text" placeholder="192.168.0.1" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("type", <select style={inputStyle} defaultValue="Prod">
          <option value="master">master</option>
          <option value="slave">slave</option>
          <option value="hint">hint</option>
        </select>, "      .", "   ")}
        {renderSetting("file", <input type="text" placeholder="" style={inputStyle} />, "  IP   .", " ")}


      </div>

      <div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
        <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}> zone </h3>
        {renderSetting("$TTL", <input type="text" placeholder="" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting(" ", <input type="text" placeholder="linux.com" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("DNS  ", <input type="text" placeholder="" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("DNS   ", <input type="text" placeholder="" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("serial", <input type="text" placeholder="" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("refresh", <input type="text" placeholder="" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("retry", <input type="text" placeholder="" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("expire", <input type="text" placeholder="" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("minimum TTL", <input type="text" placeholder="" style={inputStyle} />, "  IP   .", " ")}  

      </div>

      <div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
        <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}>   (/etc/hosts)</h3>

        {renderSetting("name server_hosts", <input type="text" placeholder="192.168.100.5" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("IP address_hosts", <input type="text" placeholder="192.168.100.5" style={inputStyle} />, "  IP   .", " ")}


      </div>  

      <div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
        <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}>    (/etc/resolv.conf)</h3>
       
        {renderSetting("name server_resolv.conf", <input type="text" placeholder="192.168.100.5" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("IP address_resolv.conf", <input type="text" placeholder="192.168.100.5" style={inputStyle} />, "  IP   .", " ")}
      </div>

      <div style={{ backgroundColor: "#313338", padding: "1.5rem", borderRadius: "8px", marginBottom: "2rem" }}>
        <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", marginBottom: "1rem" }}>    </h3>
        
        {renderSetting("", <input type="text" placeholder="" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("", renderToggle("authz_user_module"), " / ", "mod_authz_user  ")}
        {renderSetting("type-master", <select style={inputStyle} defaultValue="Prod">
          <option value="master">master</option>
          <option value="slave">slave</option>
        </select>, "      .", "   ")}
        {renderSetting("file'zone  '", <input type="text" placeholder="" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("allow-update", <input type="text" placeholder="" style={inputStyle} />, "  IP   .", " ")}
        {renderSetting("allow-transfer", <input type="text" placeholder="" style={inputStyle} />, "  IP   .", " ")}
      </div>


    </div>
  );
}

  const inputStyle = {
    padding: "6px 10px",
    width: "240px",
    backgroundColor: "#1e1f22",
    color: "white",
    border: "1px solid #555",
    borderRadius: "4px",
    textAlign: "right"
  };